.form {
    display: flex;
    justify-content: space-evenly;
    margin: 80px 50px;
    flex-wrap: wrap;

    gap: 20px;
}

.form form {
    background-color: #F7F7F7;
    box-shadow: 7px 7px 15px rgba(0, 0, 0, 25%);
    border-radius: 20px;
    padding: 8px 100px;
    flex: 1; /* espacio del 50% para cada form */
}

.formulario h2 {
    font-size: 32px;
    font-weight: 400;
    color: #212121;
}
